.breadcrumbs {
  margin-bottom: 40px;
  padding-top: calc(var(--header-height) + 20px);
  font-weight: 400;
  font-size: 15px;
  line-height: 110%;
  color: #363636;

  &__list {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__item {
    display: flex;
    align-items: center;

    &:not(:last-child)::after {
      content: "/";
      margin: 0 4px;
      color: #363636;
    }

    &:last-child {
      font-weight: 700;
    }
  }

  &__link {
    text-decoration: none;
    color: inherit;

    &:hover {
      text-decoration: underline;
    }
  }
}
