.block-description {
  &__title {
    margin-bottom: 40px;

    @include desktop {
      margin-bottom: 30px;
    }
  }

  &__wrapper {
    // display: flex;
    // justify-content: space-between;
    // gap: 40px;
    display: block;

    // @include tablet {
    //   gap: 20px;
    // }

    // @include mobile {
    //   flex-direction: column;
    // }
  }

  &__img {
    float: left; // или right — в зависимости от желаемого направления
    margin: 0 40px 20px 0; // отступы от текста
    border-radius: 30px;
    max-width: 550px; // или фиксированная ширина, если хочешь

    @include tablet {
      max-width: 450px;
    }

    @include small-tablet {
      margin: 0 20px 40px 0;
      max-width: 350px;
    }

    @include mobile {
      float: none;
      margin: 0 0 20px;
      height: 300px;
      max-width: 100%;
    }

    img {
      border-radius: 30px;

      @include mobile {
        width: 100%;
        height: 100%;
      }
    }
  }

